

.flex-container {
	display:flex;
	align-items: center;
	justify-content: space-around;
	flex-wrap: wrap;
}

.flex-item {
	display:flex;
	flex-direction:column;
	width:250px;
	height:600px;
	line-height:100px;
	background:gray;
	margin:30px;
	border:3px solid green;
	border-radius:5px;
	box-shadow:0px 0px 10px black; 
	
}
.flex-item  img {
	height:250px
}
.flex-item-img {
	display:flex;
	flex-direction:column;
	width:250px;
	height:400px;
	line-height:100px;
	background:gray;
	margin:30px;
	border:2px solid green;
	border-radius:5px;
	box-shadow:0px 0px 10px black; 
	
}

.img-show{
	background:yellow;
	flex:8;
	width:100%;
	height:250px
}

.text-container{
	background:#d0ebb2;
	flex:3;
	text-align:center;
}
.purpose-header{
	background:#d0ebb2;
	text-align:center;
}


.show-image {
	width:80%;
	height:auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
	border:2px solid green;
	border-radius:5px;
	box-shadow:0px 0px 10px black; 
}
.text-link {
    color: black;
    text-decoration: none;
}

.brownNav {
	background:#804818;
}
.brownSite{
	background:#d1bc97;
}
.container{
	padding-top:40px
}
.information-container{
	margin:auto;
	width:300px;
	border:7px solid green;
	border-radius:10px;
	padding:8px;
	box-shadow:0px 0px 10px black; 
}
.center{
	margin-left: auto;
    margin-right: auto;
}

#mainPage{
	padding-top:20px;
	width:100vw;
	height:100vh;
	text-align:center;
	background:#916e51;
	color:white;
}
:root {background:#d1bc97;}
body {padding-top:55px}



